@import url('https://fonts.googleapis.com/css2?family=Lora&display=swap');

$bgColor: rgb(37, 37, 37);

html {
    background: $bgColor;
    color: white;
    font-family: 'Lora', serif;
}

.table-of-contents {
    border: 2px solid black;
    display: flex;
    flex-direction: column;
    width: 150px;
}

.toc-element {
    color: white;
    &:hover {
        color: $bgColor;
        background: white;
    }
    padding: 5px;
}

a {
    color: rgb(77, 103, 255);
    &:visited {
        color: rgb(116, 77, 255);
    }
    text-decoration: none;
}

.code {
    font-style: italic;
    &.inline {

    }
}

.line-jump {
    margin: 15px;
}

.highlight {
    background-color: gray;
}

.anecdote {
    border: gray solid 1px;
}

$page-elems-margin: 25px;
.page-layout {
    display: flex;
    .column-1 {
        margin: $page-elems-margin;
    }
    .column-2 {
        margin: $page-elems-margin;
        margin-left: 0;
    }
}